function totalLength(){
	return expChartPlayer.length;
}

function playerDataLength(curLevel, curMaxLevel){
	return expChartPlayer.slice(curLevel, (curMaxLevel-1)).length;
}

function removeExcessPlayerData(curLevel){
	return expChartPlayer.slice(curLevel);
}

function totalExp(curLevel){
	let exp = removeExcessPlayerData(curLevel);
	let total = 0;
	exp.forEach(function(data){
		return total + data;
	});

	return total;
}

function expNeededFromLevelPlayer(curLevel){
	if(curLevel === 70){
		return 0;
	}
	return expChartPlayer[curLevel];
}

function expNeededFromLevelPOTD(curLevel){
	var exp = expChartPOTD[curLevel];
	if(curLevel === 70){
		exp = 0;
	}
	return {
		exp: exp,
		exp50: exp * 1.5,
		exp100: exp * 2
	}
}

function runsNeeded(expPlayer, expPOTD){
	if(!expPlayer || !expPOTD){
		return 0;
	}
	return Math.ceil(expPlayer / expPOTD);
}

function totalRunsNeeded(level, totalRows, currentExp){
	let expPlayer, curLevel;
	let i = 0;
	let expPOTD = {};

	let runsArr = {
		runs: 0,
		runs50: 0,
		runs100: 0,
	};

	while(i <= totalRows){
		curLevel = level + i;
		expPlayer = (i===0) ? expNeededFromLevelPlayer(curLevel)-currentExp : expNeededFromLevelPlayer(curLevel);
		expPOTD = expNeededFromLevelPOTD(curLevel);
		runsArr.runs = runsArr.runs + runsNeeded(expPlayer, expPOTD.exp);
		runsArr.runs50 = runsArr.runs50 + runsNeeded(expPlayer, expPOTD.exp50);
		runsArr.runs100 = runsArr.runs100 + runsNeeded(expPlayer, expPOTD.exp100);
		i++
	}

	return runsArr;
}

function calcTime(runs, type){
	let total, hours, minutes = 0;
	if(type === "quick"){
		total = runs * 10;
	} else if(type === "average"){
		total = runs * 15;
	} else{
		total = runs * 20;
	}
	hours = Math.floor(total / 60);
	minutes = Math.floor(total - (hours * 3600) / 60);
	if (minutes < 10) {minutes = "0"+minutes;}
	return hours+":"+minutes;
}

module.exports = {
    totalLength: totalLength,
    playerDataLength: playerDataLength,
    expNeededFromLevelPlayer: expNeededFromLevelPlayer,
    expNeededFromLevelPOTD: expNeededFromLevelPOTD,
    totalExp: totalExp,
    runsNeeded: runsNeeded,
    totalRunsNeeded: totalRunsNeeded,
    calcTime: calcTime
};


