import React from 'react';
import InputComponent from './input-component';
import ButtonComponent from './button-component';
import * as LevelData from './data/exp-chart';
import completedRun from './data/run-functions';

export default class InfoEntryComponent extends React.Component {
	onChange(fieldName, fieldValue) {
        // parent class change handler is always called with field name and value
        this.props.onChange(fieldName, fieldValue);
    }

    onClick(fieldName, fieldValue) {
        const curLevel = this.props.curLevel;
        const curExp = this.props.curExp;
        let data;
        if(fieldName==="runNone"){
        	data = completedRun(curLevel, curExp, "runNone");
        } else if(fieldName==="runFifty"){
        	data = completedRun(curLevel, curExp, "runFifty");
        } else {
        	data = completedRun(curLevel, curExp, "runHundred");
        }
        this.props.onClick("curLevel", data.curLevel);
        this.props.onClick("curExp", data.curExp);
    }

  	render() {
  		const curLevel = this.props.curLevel;
  		const curMaxLevel = this.props.curMaxLevel;
  		let maxValue = LevelData.expNeededFromLevelPlayer(curLevel);
		return (
			<div className={this.props.className}>
		  		<div className='infoRow'>
		          <div className='infoTitle'>Current Level:</div>
		          <div className='infoData'>
		            <InputComponent
		              type="number"
		              className="curLevel"
		              inputValue={this.props.curLevel}
		              min="0"
		              max={curMaxLevel-1}
		              onChange={this.onChange.bind(this)}
		             />
		          </div>
		        </div>
		        <div className='infoRow'>
		          	<div className='infoTitle'>Current Experience Points:</div>
		          	<div className='infoData'>
						<InputComponent
							type="number"
							className="curExp"
							inputValue={this.props.curExp}
							min="0"
							max={maxValue}
							onChange={this.onChange.bind(this)}
						/>
		        	</div>
		        </div>
		        <div className='infoRow'>
		          	<div className='infoTitle'>Level to Reach:</div>
		          	<div className='infoData'>
						<InputComponent
							type="number"
							className="curMaxLevel"
							inputValue={this.props.curMaxLevel}
							min={curLevel+1}
							max="70"
							onChange={this.onChange.bind(this)}
						/>
		        	</div>
		        </div>
		        <div className='infoRow'>
		          	<div className='infoButton'>
						<ButtonComponent
							className="runNone"
							defaultValue="70"
							textElement="Complete Run! (0% Armoury)"
							onMouseUp={this.onClick.bind(this)}
						/>
		        	</div>
		          	<div className='infoButton'>
						<ButtonComponent
							className="runFifty"
							defaultValue="70"
							textElement="Complete Run! (50% Armoury)"
							onMouseUp={this.onClick.bind(this)}
						/>
		        	</div>
		          	<div className='infoButton'>
						<ButtonComponent
							className="runHundred"
							defaultValue="70"
							textElement="Complete Run! (100% Armoury)"
							onMouseUp={this.onClick.bind(this)}
						/>
		        	</div>
		        </div>
		    </div>
		);
  	}
};

InfoEntryComponent.defaultProps = {
	className:"info-entry-general",
	curLevel: 1,
	curExp: 0,
	curMaxLevel: 70
};


