import React, { Component } from 'react';
import logo from './logo.svg';
import InfoEntryComponent from './components/info-entry-component'
import InfoGeneratedComponent from './components/info-generated-component'
import './App.css';
import './css/react-bootstrap/react-bootstrap-table-all.min.css';


class App extends Component {
	constructor(props) {
      super(props);
      this.state = {
        curLevel: 1,
        curExp: 0,
        curMaxLevel: 70
      }
    }
	onChange(field, value) {
	    this.setState({[field]: value});
	}
  	render() {
	    return (
	      <div className="container">
	      	<h1>FFXIV Palace of the Dead Run Calculator</h1>
	        <InfoEntryComponent 
	        	curLevel = {this.state.curLevel}
	        	curExp = {this.state.curExp}
	        	onChange={this.onChange.bind(this)}
	        	onClick={this.onChange.bind(this)}
	        	curMaxLevel = {this.state.curMaxLevel}
	        />
	        <InfoGeneratedComponent
		        curLevel = {this.state.curLevel}
		        curExp = {this.state.curExp}
		        curMaxLevel = {this.state.curMaxLevel}
	        />
	      </div>
	    );
  	}
}

export default App;



