import React from 'react';

export default class ButtonComponent extends React.Component {
	onMouseUpEvent(event) {
        // for a regular input field, read field name and value from the event
        const fieldName = event.target.className;
        this.props.onMouseUp(fieldName, true);
    }

  	render() {
		return (
	  		<button className={this.props.className} defaultValue={this.props.defaultValue} onMouseUp={this.onMouseUpEvent.bind(this)}>
	  			{this.props.textElement}
	  		</button>
		);
  	}
};

ButtonComponent.defaultProps = {
	className:"button-general",
	defaultValue:""
};


