import React from 'react';
import DataTable from './data-table-component'

export default class InfoGeneratedComponent extends React.Component {
  	render() {
		return (
			<div className={this.props.className}>
		  		<DataTable 
		  			isSummary={true}
		  			currentLevel = {this.props.curLevel}
		  			currentExp = {this.props.curExp}
		  			currentMaxLevel = {this.props.curMaxLevel}
		  			className="summary-view"
		  		/>
		  		<DataTable 
		  			isSummary={false}
		  			currentLevel = {this.props.curLevel}
		  			currentExp = {this.props.curExp}
		  			currentMaxLevel = {this.props.curMaxLevel}
		  			className="detail-view"
		  		/>
		    </div>
		);
  	}
};

InfoGeneratedComponent.defaultProps = {
	className:"info-entry-general",
};


