import * as LevelData from './exp-chart';

export default function completedRun(level, exp, type, hasExp){
	let expGained = LevelData.expNeededFromLevelPOTD(level); // 15
	let expNeeded = LevelData.expNeededFromLevelPlayer(level); //10
	let runExperience;
	let newExpNeeded;

	if(type==="runNone"){
		runExperience = expGained.exp; //15
	} else if(type==="runFifty"){
		runExperience = expGained.exp50;
	} else {
		runExperience = expGained.exp100;
	}

	if(hasExp){
		if(hasExp >= expNeeded){
			hasExp = hasExp - expNeeded;
			return completedRun((level+1), 0, type, hasExp);
		} else {
			return {
				curLevel: level,
				curExp: hasExp
			};
		}
	} else{
		newExpNeeded = expNeeded - exp; //8

		if(runExperience >= newExpNeeded){
			runExperience = runExperience - newExpNeeded;
			return completedRun((level+1), 0, type, runExperience);
		} else {
			return {
				curLevel: level,
				curExp: newExpNeeded
			};
		}
	}
}


