import React from 'react';
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';
import * as LevelData from './data/exp-chart';

export default class DataTable extends React.Component {
  	render() {
		const isSummary = this.props.isSummary;
		let level = this.props.currentLevel;
		let maxLevel = this.props.currentMaxLevel;
		let currentExp = this.props.currentExp;
		let data = [];
		let dataTime = [];
		if(!isSummary){
			let totalRows = LevelData.playerDataLength(level, maxLevel);
			let i = 0;
			let currentLevel = 0;
			let expPOTD = {};
			let expPlayer = 0;
			while(i <= totalRows){
				currentLevel = (level + i);
				expPlayer = (i===0) ? LevelData.expNeededFromLevelPlayer(currentLevel)-currentExp : LevelData.expNeededFromLevelPlayer(currentLevel);
				expPOTD = LevelData.expNeededFromLevelPOTD(currentLevel);
				data.push({
					id: currentLevel, 
					expPlayer: expPlayer,
					expPOTD: expPOTD.exp,
					expPOTDArmoryFifty: expPOTD.exp50,
					expPOTDArmoryHundred: expPOTD.exp100,
					runsNeeded: LevelData.runsNeeded(expPlayer, expPOTD.exp),
					runsNeededFifty: LevelData.runsNeeded(expPlayer, expPOTD.exp50),
					runsNeededHundred: LevelData.runsNeeded(expPlayer, expPOTD.exp100)
				});
				i++;
			}

			return (
			  	<BootstrapTable data={ data } className={this.props.className}>
			        <TableHeaderColumn row='0' rowSpan='2' dataField='id' isKey>Level</TableHeaderColumn>
			        <TableHeaderColumn row='0' rowSpan='2' dataField='expPlayer'>Exp Required</TableHeaderColumn>
			        <TableHeaderColumn row='0' colSpan='3' headerAlign='center'>Exp Awarded by Floor 51-60</TableHeaderColumn>
			        <TableHeaderColumn row='1' dataField='expPOTD'>No Armoury</TableHeaderColumn>
			        <TableHeaderColumn row='1' dataField='expPOTDArmoryFifty'>50% Armoury</TableHeaderColumn>
			        <TableHeaderColumn row='1' dataField='expPOTDArmoryHundred'>100% Armoury</TableHeaderColumn>
			        <TableHeaderColumn row='0' colSpan='3' headerAlign='center'>Number of Runs</TableHeaderColumn>
			        <TableHeaderColumn row='1' dataField='runsNeeded'>No Armoury</TableHeaderColumn>
			        <TableHeaderColumn row='1' dataField='runsNeededFifty'>50% Armoury</TableHeaderColumn>
			        <TableHeaderColumn row='1' dataField='runsNeededHundred'>100% Armoury</TableHeaderColumn>
			    </BootstrapTable>
			);
		} else {
			let totalRows = LevelData.playerDataLength(level, maxLevel);
			let runsPOTD = LevelData.totalRunsNeeded(level, totalRows, currentExp);
			data.push({
				runsNeeded: runsPOTD.runs || 0 ,
				runsNeededFifty: runsPOTD.runs50 || 0,
				runsNeededHundred: runsPOTD.runs100 || 0
			});

			dataTime.push({
				runType: "Fast Runs - 10 minutes",
				runsNeeded: LevelData.calcTime(runsPOTD.runs, "quick"),
				runsNeededFifty: LevelData.calcTime(runsPOTD.runs50, "quick"),
				runsNeededHundred: LevelData.calcTime(runsPOTD.runs100, "quick")
			});

			dataTime.push({
				runType: "Average Runs - 15 minutes",
				runsNeeded: LevelData.calcTime(runsPOTD.runs, "average"),
				runsNeededFifty: LevelData.calcTime(runsPOTD.runs50, "average"),
				runsNeededHundred: LevelData.calcTime(runsPOTD.runs100, "average")
			});

			dataTime.push({
				runType: "Slow Runs - 20 minutes",
				runsNeeded: LevelData.calcTime(runsPOTD.runs, "slow"),
				runsNeededFifty: LevelData.calcTime(runsPOTD.runs50, "slow"),
				runsNeededHundred: LevelData.calcTime(runsPOTD.runs100, "slow")
			});

			return (
				<div>
				  	<BootstrapTable data={ data } className={this.props.className}>
				        <TableHeaderColumn row='0' colSpan='3' headerAlign='center'>Number of Runs</TableHeaderColumn>
				        <TableHeaderColumn row='1' dataField='runsNeeded' isKey>No Armoury</TableHeaderColumn>
				        <TableHeaderColumn row='1' dataField='runsNeededFifty'>50% Armoury</TableHeaderColumn>
				        <TableHeaderColumn row='1' dataField='runsNeededHundred'>100% Armoury</TableHeaderColumn>
				    </BootstrapTable>

				    <BootstrapTable data={ dataTime } className={this.props.className}>
				    	<TableHeaderColumn row='0' rowSpan='2' dataField='runType' isKey>Run Time</TableHeaderColumn>
				        <TableHeaderColumn row='0' colSpan='3' headerAlign='center'>Average Time to complete</TableHeaderColumn>
				        <TableHeaderColumn row='1' dataField='runsNeeded'>No Armoury</TableHeaderColumn>
				        <TableHeaderColumn row='1' dataField='runsNeededFifty'>50% Armoury</TableHeaderColumn>
				        <TableHeaderColumn row='1' dataField='runsNeededHundred'>100% Armoury</TableHeaderColumn>

				    </BootstrapTable>
				</div>
			);
		}
  	}
};

DataTable.defaultProps = {
	className:"table-fill",
	currentLevel: 1,
	currentMaxLevel: 70,
	currentExp: 299
};


