import React from 'react';

export default class InputComponent extends React.Component {
	onFieldChange(event) {
        // for a regular input field, read field name and value from the event
        const fieldName = event.target.className;
        const fieldValue = parseInt(event.target.value, 10);
        this.props.onChange(fieldName, fieldValue);
    }

  	render() {
		return (
	  		<input type={this.props.type} className={this.props.className} value={this.props.inputValue} max={this.props.max} min={this.props.min}  onChange={this.onFieldChange.bind(this)} />
		);
  	}
};

InputComponent.defaultProps = {
	type:"text",
	className:"input-general",
	inputValue: "",
	min:"",
	max:""
};


